// Normalize Styles
@import 'node_modules/normalize.css/normalize';
@import 'node_modules/aos/dist/aos';

// Import Modules
// @import '../_modules/link/link';

@font-face {
  font-family: 'Maison';
  src: url('../fonts/MaisonNeueTRIAL-Mono.otf');
}

@font-face {
  font-family: 'GoogleSans';
  src: url('../fonts/4UaGrENHsxJlGDuGo1OIlL3Owp5eKQtG.woff2');
}

@import url('https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap');



// Box sizing partial styles:
// Apply a natural box layout model to all elements
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  font-family: 'GoogleSans';

  color: white;

  background: black;
}

body {
  min-height: 100vh;

  height: 100vh;
}

.container {
  max-width: 1280px;
  padding: 0 40px;

  margin: 0 auto;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

h1 {
  font-size: 20px;

  font-family: 'Maison';
}

.site-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  padding-top: 36px;

  a {
    text-decoration: none;
  }
  
}

.home {
  height: 100%;

  display: flex;
  flex-direction: column;

  &__information {
    align-items: center;

    height: 100%;


    &--inner {
      max-width: 600px;
    }
  }

}

p {
  font-size: 27px;

  color: grey;

  font-weight: 300;

  letter-spacing: .04em;
}

.desc {
  font-size: 32px;
  color: white;

  white-space: nowrap;
}

a {
  color: white;

  font-weight: 600;
}


.curriculum-vitae {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 36px;
}


@media (max-width: 1024px) {
  .container {
    max-width: 100%;
  }

  .home {

    &__information {

      &--inner {
        max-width: 100%;
      }

    }

  }

  .desc {
    white-space: normal;
  }

}


